import React, { useEffect } from "react";
import "../Styles/workpage.scss";
import Work from "../Components/WorkPageLogic";
import Title from "../Components/Title";
import Footer from "../Components/Footer";

function WorkPage({
  history,
  page,
  setPage,
  pageHandler,
  project,
  setProject,
}) {
  useEffect(() => {
    pageHandler("work");
  }, []);
  return (
    <div>
      <Title />
      <section>
        <Work
          name="Company Stock Information Portal
"
          id="1"
          setProject={setProject}
          project={project}
          link="/details"
          src="https://i.imgur.com/fs5W3OS.png"
          description="This project is a private stock information 'gather & display' portal where users can search stock information for any company they need. It connects to multiple API calls and saves the data to our internal database that sorts and displays various information, which can be filtered and saved. "
          span="On this project i created the entire Fronted in Angular and SASS mainly."
          liveDemo=""
          lang1="fab fa-css3-alt"
          lang2="fab fa-html5"
          lang3="fab fa-js-square"
          lang4="fab fa-sass"
          lang5="fab fa-angular"
        />
        <Work
          name="Personal Portfolio
"
          id="2"
          setProject={setProject}
          project={project}
          link="/details"
          src="https://i.imgur.com/Asq8d40.png"
          description="This is my personal portfolio page where i have practiced and tried to used latest methods of building a web app."
          span="For this project i have used React and SASS mainly and its being hosted on Github Pages."
          readMore=""
          liveDemo="https://zenandreas.github.io"
          gitHub="https://github.com/zenandreas/portfolio"
          lang1="fab fa-css3-alt"
          lang2="fab fa-html5"
          lang3="fab fa-js-square"
          lang4="fab fa-sass"
          lang5="fab fa-react"
        />
        <Work
          name="GitHub user search app
"
          id="2"
          setProject={setProject}
          project={project}
          link="/details"
          src="https://i.imgur.com/HYL6ucp.png"
          description="This is a simple web app that hooks into the Github API and pulls user information via the search form. The purpose of this project was to successfully connect to Github's API and collect user information."
          span="This is a project i took from FrontendMentor.io website which provides random frontend challenges with various tehniques and recommendations."
          readMore=""
          liveDemo="https://zenandreas.github.io/GitHub-Search/index.html"
          gitHub="https://github.com/zenandreas/zenandreas.github.io/tree/master/GitHub-Search"
          lang1="fab fa-css3-alt"
          lang2="fab fa-html5"
          lang3="fab fa-js-square"
          lang4="fab fa-sass"
        />
        <Work
          name="Timetracking App

"
          id="3"
          setProject={setProject}
          project={project}

          link="/details"
          src="https://i.imgur.com/Glzorsl.png"
          description="This is a simple web app that reads a json database and renders out informations based on your activities. The purpose of this project was to callenge my CSS skills and localStorage knowledge."
          span="This was another FrontendMentor.io challenge and was completed using Html, Css and Javascript."
          readMore=""
          liveDemo="https://zenandreas.github.io/timetracking-app/index.html"
          lang1="fab fa-css3-alt"
          lang2="fab fa-html5"
          lang3="fab fa-js-square"
          lang4="fab fa-sass"
        />
        <Work
          name="Travel Agency Demo Website

"
          id="4"
          setProject={setProject}
          project={project}
          link="/details"
          src="https://i.imgur.com/TMA226v.png"
          description="A simple website made for a demo project. This was ment for me to practice with the website animation systems such as gsap and scrollMagic."
          span="This was built using HTML/CSS/JAVASCRIPT"
          readMore=""
          gitHub="https://github.com/zenandreas/zenandreas.github.io/tree/master/Travel%20Project"
          liveDemo="https://zenandreas.github.io/Travel%20Project/"
          lang1="fab fa-css3-alt"
          lang2="fab fa-html5"
          lang3="fab fa-js-square"
        />
        <Work
          name="A simple image searcher

"
          id="5"
          setProject={setProject}
          project={project}
          link="/details"
          src="https://i.imgur.com/kbPCvVR.png"
          description="A simple website made for a demo project. This was ment for me to practice with API connections."
          span="This was built using HTML/CSS/JAVASCRIPT"
          readMore=""
          liveDemo="https://zenandreas.github.io/Photon/index.html"
          lang1="fab fa-css3-alt"
          lang2="fab fa-html5"
          lang3="fab fa-js-square"
        />
      </section>
      <Footer />
    </div>
  );
}

export default WorkPage;
