import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import "../Styles/navbar.scss";
import Home from "../Pages/Home";
import Work from "../Pages/WorkPage";
import Details from "../Pages/Details";
import { withRouter } from "react-router-dom";

import PageNotFound from "../Pages/404-page";

function Navbar({ history, page, setPage, pageHandler }) {
  const [locationWork, setlocationWork] = useState(false);
  const [locationHome, setlocationHome] = useState(false);
  const [project, setProject] = useState("");
  useEffect(() => {
    checkPage(history);
  }, [page]);
  // Show the hamburger menu container when clicked
  function showHam() {
    const hamburger = document.querySelector(".hamburger-nav");
    hamburger.classList.toggle("visible-hamburger");
  }
  // Hides the hamburger menu container when clicked
  function hideHam() {
    const hamburger = document.querySelector(".hamburger-nav");
    hamburger.classList.remove("visible-hamburger");
  }

  function checkPage(history) {
    if (history.location.pathname === "/work") {
      setlocationWork(true);
      setlocationHome(false);
    } else if (history.location.pathname.split("/")[1] === "details") {
      setlocationHome(false);
      setlocationWork(false);
    } else {
      setlocationHome(true);
      setlocationWork(false);
    }
  }
  return (
    <div className="App">
      <ul className="navbar">
        <li className="home-link">
          <Link to={"/"}>Home</Link>
          <hr className={"home-hr" + (locationHome ? " locationHome" : "")} />
        </li>
        <li className="work-link">
          <Link to={"/work"}>Work</Link>
          <hr className={"work-hr" + (locationWork ? " locationWork" : "")} />
        </li>
        <li className="resume">
          <a
            href="https://drive.google.com/file/d/1Qx_T7BRFiQ3GfDC6eKaSvzDddFAAUyHr/view?usp=sharing"
            target="_blank rel="
            rel="noopener noreferrer"
          >
            <i className="fas fa-file-download"></i>
          </a>
        </li>
      </ul>

      <i className="fas fa-bars" id="hamburger" onClick={showHam}></i>
      <div className="hamburger-nav">
        <ul className="hamburger-navbar">
          <li className="home-link hamburger">
            <Link to={"/"} onClick={hideHam}>
              Home
            </Link>
          </li>
          <li className="work-link hamburger">
            <Link to={"/Work"} onClick={hideHam}>
              Work
            </Link>
          </li>
          <li className="resume-hamburger">
            <a
              href="https://drive.google.com/file/d/16wjNQjQWwuNDozpnRXfYLRWy_gWoJ-O3/view?usp=sharing"
              target="_blank rel="
              rel="noopener noreferrer"
            >
              Resume
              <i className="fas fa-file-download"></i>
            </a>
          </li>
        </ul>
      </div>
      <Switch>
        <Route
          path="/"
          exact
          render={(props) => (
            <Home
              {...props}
              history={history}
              page={page}
              setPage={setPage}
              pageHandler={pageHandler}
            />
          )}
        />

        <Route
          path="/work"
          exact
          render={(props) => (
            <Work
              {...props}
              history={history}
              page={page}
              setPage={setPage}
              pageHandler={pageHandler}
              project={project}
              setProject={setProject}
            />
          )}
        />
        <Route
          path="/details/:id"
          exact
          render={(props) => (
            <Details
              {...props}
              history={history}
              page={page}
              setPage={setPage}
              pageHandler={pageHandler}
              project={project}
              setProject={setProject}
            />
          )}
        />

        <Route component={PageNotFound} />
      </Switch>
    </div>
  );
}

export default withRouter(Navbar);
