import React from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

import "../Styles/work.scss";

export function Work(props) {
  return (
    <div className="work-container">
      <div className="work-description">
        <h2 className="work-name">
          <Link
            to={`${props.link}/${props.id}`}
            onClick={() => props.setProject(props.id)}
          >
 {props.name}
          </Link>
        </h2>  
         
        <div className="description">
          {props.description}
          <span>{props.span}</span>
        </div>

        <div className="buttons">
          <a href={props.liveDemo} target="_blank" rel="noreferrer">
            {props.liveDemo && (
              <button type="" className="btn btn-primary">
                Check Live Demo
              </button>
            )}
          </a>
          <a href={props.readMore} target="_blank" rel="noreferrer">
            {props.readMore && (
              <button type="" className="btn btn-primary">
                Check Live Demo
              </button>
            )}
          </a>
          <a href={props.gitHub} target="_blank" rel="noreferrer">
            {props.gitHub && (
              <button type="" className="btn btn-primary">
                Check Code
              </button>
            )}
          </a>
        </div>
      </div>
      <div className="work-thumbnail">
        <img src={props.src} alt={props.name} />
        <div className="work-thumbnail-shadow-box"></div>
        <div className="skills-used">
          <i className={props.lang1}></i>
          <i className={props.lang2}></i>
          <i className={props.lang3}></i>
          <i className={props.lang4}></i>
          <i className={props.lang5}></i>
        </div>
      </div>
    </div>
  );
}

export default Work;
