import React from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import SyntaxHighLighter from "react-syntax-highlighter";
import { dracula } from "react-syntax-highlighter/dist/cjs/styles/hljs";

import "../Styles/Details.scss";

export function ProjectDetailsPage(props) {
  return (
    <>
      <section className="workinfo-container">
        {props.history.location.pathname.split("/")[2] === "1" && (
          <div className="workinfo-container__stockpicker">
            <div className="title">
              <Link to="/work">
                <i class="fas fa-long-arrow-alt-left"></i>
              </Link>
              <h1>Company Stock Information Portal</h1>
            </div>

            <div className="workinfo-container__stockpicker__tech">
              <h4>Tech Stack</h4>
              <div className="skill-container">
                <i className="fab fa-angular"></i>
                <i className="fab fa-html5"></i>
                <i className="fab fa-js-square"></i>
                <i className="fab fa-sass"></i>
                <p>Date published: 16.12.2021</p>
                <p>Author: Mircea Dumitrescu</p>
                <p>Github Project: Private</p>
                <p>Employer: MinDance SRL</p>
              </div>
            </div>
            <div className="workinfo-container__stockpicker__article">
              <div className="row-introduction">
                <div className="row-introduction__banner">
                  <img
                    src="https://i.imgur.com/TEPYKin.png"
                    alt="stockpicker"
                  />
                </div>
                <div className="row-introduction__text">
                  <h1>Introduction</h1>
                  <div className="row-introduction__text__container">
                    <p>
                      This is a web application that allows users to search for
                      informations and relevant data regarding any company
                      enlisted in open market. The application is built with
                      Angular and uses REST API to retrieve data from our
                      Java/SQL backend setup. We have over 80k companies in our
                      database and we are constantly fetching new data via our
                      third party API providers. The application has complex
                      user interface features which allows the user to have
                      personalized filters, news and recommendations based on
                      their previous searches and preferences.
                    </p>
                  </div>
                </div>
              </div>

              <div className="row-more-info">
                <div className="row-more-info__text">
                  <h1>My Role in the project</h1>

                  <p>
                    In a very small team i was the main developer responsible
                    for the frontend development. I was responsible for the
                    implementation of the frontend components and the styling of
                    the application. I was also responsible for the
                    implementation of the REST API and overall frontend
                    architecture.
                  </p>
                  <h3>
                    Here are some of the features which i found interesting and
                    the way i approached solving them:
                  </h3>
                  <h3>1. Playing around with deBounce to reduce fetches</h3>
                  <p>
                    I had to play around with search inputs to see if I could
                    reduce the number of fetches. I found that the Lodash decorator
                    @Debounce did exactly what i was looking for and it was
                    very easy to implement. Here are just some code examples where
                    it was used. Mainly in the primary search input field on the
                    Navbar and in the search input field on the All Company list,
                    where multiple filters can be applied and you can Search
                    via multiple inputs and variables.
                  </p>
                  <p>In this case, the checkResponse functions verifies if the search
                    returns any data, if not, it will display a message to the user
                    with 100ms delay more than the search fetch. The guardCheck function
                    is much more complex than i have pasted here obviously, but it
                    basicly handles the case where the user enters a search term that
                    is not in the database (company name or description).
                  </p>
                  <div className="row-code-example">
                    <SyntaxHighLighter language="javascript" style={dracula}>
                      {
                        "\n@Debounce(400)\ncheckResponse() {\nthis.doWeHavedata = this.companies.length === 0 ? false : true;}\n\n@Debounce(300)\nguardCheck(input: any, filter: string) {\nif (\nfilter === this.checkedSearch.companyName.name ||\nfilter === this.checkedSearch.companyDescription.name)"
                      }
                    </SyntaxHighLighter>
                  </div>
                  <h3>
                    2. Creating a favorite company option for user profile
                  </h3>
                  <p>
                    One interesting approach i had to take when implementing
                    the favorite component for each user. Users are able to save
                    companies as "favorite companies" and have a page where they
                    can see and remove them. Also, based on their preferences,
                    i had to display certain news related to their companies or industries.
                  </p>
                  <p>To have a persistent way of saving this data we have eventually
                    decided to go with a database solution + an Angular Service to fetch
                    the favorite list whenever we needed it. This is important, because we had
                    to make multiple checks on different pages where the Add to favorite or remove
                    buttons were also located.
                  </p>
                  <div className="row-code-example">
                    <SyntaxHighLighter language="javascript" style={dracula}>
                      {
                        "\nconstructor(private apiService: apiService)\n  fetchSavedCompanies() {\nthis.apiService.getUserCompany().subscribe((input) => {\nthis.companies = input.companies;\nObject.keys(this.companies).forEach((key) => {\nif (this.companies[key].company.tickSymbol === this.companyNameShort) {\nthis.isCompanySaved = true;}});});}\n\ntoggleSaveDelete() {\nif (this.isCompanySaved) {\nthis.apiService.deleteUserCompany(this.companyNameShort).subscribe(() => {\nthis.fetchSavedCompanies();\nthis.isCompanySaved = false;\nthis.modalService.openSnackBar(\n'Company Removed from your list',\n'DELETED'\n);});\n} else {\nthis.apiService.saveCompany(this.companyNameShort).subscribe(() => {\nthis.fetchSavedCompanies();});}}"}
                    </SyntaxHighLighter>
                  </div>
                  <h3>
                    3. Filtering companies based on preferences and values
                  </h3>
                  <p>
                    This was by far the most complex feature we had to implement. The way we approached this
                    was by combining database with localstorage. We had to make sure that the user had the
                    ability to save their preferences and values and that they could see them on the
                    profile page. Also, we wanted to save some of the filters if the user left the page and came back.
                  </p>
                  <p>
                    I had to play with alot of Services and localstarge instances to be able to achieve my goal and this challenge
                    was not necesarelly hard to code, but it was harder because i had to decide which approach was the best approach,
                    for our website, but also for user experience.
                  </p>
                  <div className="row-code-example">
                    <SyntaxHighLighter language="javascript" style={dracula}>
                      {
                        "\n@Debounce(1000)\ninitialLoad() {\nif (this.searchService.getFilteredCompanies().length === 0) {\nthis.filterParams.page = 0;\nthis.filterParams.sortDirection = 'ASC';\n} else {\nthis.filterParams = this.searchService.getFilteredCompanies();\nthis.companyName = this.filterParams.name;\nthis.companyDescription = this.filterParams.description;\nthis.companyPE = this.filterParams.PE;\nthis.companyRG = this.filterParams.revenueGrowth;\nthis.activeParams = [this.filterParams];\n}\ntry {\nthis.fetchApi(this.filterParams);\nthis.showSpinner = false;\n} catch (error) {\nconsole.log(error);\n}\nconst localDB: any = localStorage.getItem('checkedSearch');\nthis.checkedSearch = localDB ? JSON.parse(localDB) : this.checkedSearch;\n}"}
                    </SyntaxHighLighter>
                  </div>
                </div>
              </div>
              <div className="row-second-example">
                <div className="row-second-example__images">
                  <div className="code_structure">
                    <p>Code Structure</p>
                    <a href="https://i.imgur.com/HZYZEOA.png">
                      <img
                        src="https://i.imgur.com/HZYZEOA.png"
                        alt="stockpicker"
                      />
                    </a>
                  </div>
                  <div className="filter_structure">
                    <p>Filters Structure</p>

                    <a href="https://i.imgur.com/8PSxIiO.png">
                      <img
                        src="https://i.imgur.com/8PSxIiO.png"
                        alt="stockpicker"
                      />
                    </a>
                  </div>
                </div>
                <div className="row-more-info__text">
                  <h1>Conclusion</h1>
                  <p>
                    This project was a great experience for me. I learned a lot,
                    especially how to properly plan and implement a project.
                    Being solely responsible for the architecture and frontend
                    development, I was able to create a great user experience.
                  </p>
                  <Link to="/work">
                    <i className="fas fa-long-arrow-alt-left"></i> Go back
                  </Link>
                </div>
              </div>
            </div>
          </div>
        )}
      </section>
      <section className="workinfo-container">
        {props.history.location.pathname.split("/")[2] != "1" && (
          <div className="work-in-progress">Work in progress.                <Link to="/work">Go back
        </Link></div>
        )}
        </section>
    </>
  );
}

export default ProjectDetailsPage;
