import React from "react";
import "../Styles/404-page.scss";

const PageNotFound = () => {
  setTimeout(() => {
    window.location.href = "/";
  }, 5000);

  return (
    <div id="wrapper">
      <div id="info">
        <h3>This page could not be found</h3>
      </div>
      <img src="https://i.imgur.com/qIufhof.png" />
    </div>
  );
};

export default PageNotFound;
