import React, { Component } from "react";
import "../Styles/description.scss";

export class Description extends Component {
  render() {
    return (
      <div className="description-container">
        <div className="colored-indent">
          <hr />
        </div>
        <div className="description">
          <p>
            Working in the IT industry for the past 10 years as a Project
            Manager(gaming/development industries) i have decided to switch to a
            development position and since the beginning of 2020 i have started
            learning coding.
          </p>
          <h4 className="h4">
            Here is a short description of my past year activities:
          </h4>
          <ul>
            <li>
              I first began with Python and Ruby and reached a level which
              allowed me to create a few small apps and get a grasp of what i
              like , specifically, which language to continue with.
            </li>
            <li>
              I already knew HTML/CSS but improved my skills a lot, by
              completing several courses online and practicing on some
              freelancing and volunteering projects (open source).
            </li>
            <li>
              Currently mastering Javascript/Typescript and doing projects in
              React or Angular. In the past months i have primarily been
              focusing on Angular and how to use it to create a better user
              experience.
            </li>
            <li>
              I am currently enrolled in two courses: ( Full Stack Developer
              from new tech academy Romania and HarvardX CS50x course from
              edx.org ).
            </li>
          </ul>
          <p>
            I am extremely dedicated and passionate about what i am doing and
            would love to learn even more, in a dedicated environment with a
            team formed by people with the same drive i have.
          </p>
        </div>
      </div>
    );
  }
}

export default Description;
